.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100vh;
  text-align: center;
  padding-left: 160px;
  background: radial-gradient(
    300% 100% at top center,
    #1b2947 10%,
    var(--clr-main-darker) 100%
  );
}


.container {
  max-width: 70rem;
  width: 100%;
  position: relative;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  --family: var(--ff-poiret);

  h1 {
    text-align: left;
    margin: 0;
    font-size: 6rem;
    color: white;
    font-family: var(--family);
  }

  p {
    color: white;
    text-align: left;
    margin: 15px 0 0 0;
    font-size: var(--fs-600);
    font-family: var(--family);

    display: block;
  }

  span {
    font-family: var(--family);
    font-size: var(--fs-600);
    color: #1da1f2;
  }

  .socials {
    padding: 1.5rem 0;

    div {
      justify-content: left;
      --color: white;
      --bg-color: none;
      --font-size: 1.5rem;
    }
  }
}

@media (max-width: 600px) {
  .hero {
    padding-left: 0;
    padding: 5px;
  }

  .container {
    padding-left: 0;
    padding-right: 0;

    h1 {
      font-size: 5rem;
      text-align: center;
    }

    p {
      font-size: 2rem;
      text-align: center;
    }

    .socials {
      div {
        justify-content: center;
      }
    }
  }
}
